import { Component, OnInit } from '@angular/core';
import { FactsService } from '../facts.service';
import { SelectItem } from 'primeng/components/common/selectitem';

@Component({
  selector: 'app-facts',
  templateUrl: './facts.component.html',
  styleUrls: ['./facts.component.css']
})
export class FactsComponent implements OnInit {

  facts: any[];
  manufacturerOptions: SelectItem[];

  originalFactsList: any[];

  constructor(private factService: FactsService) {
    this.factService.getFacts().subscribe(
      data => {
        this.facts = data;
        this.originalFactsList = data;
      }
    );
  }

  ngOnInit() {
  }

  filterFacts(manufacturer: string) {
    this.facts = this.originalFactsList;
    if (manufacturer) {
      this.facts = this.originalFactsList.filter(fact => fact.manf === manufacturer);
    }
  }

}
