import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';

export interface BeerLocation {
  bar: string;
  price: number;
  customers: number;
}
export interface Barshit{
  name: string;
  totalSold: number;
}
export interface Hourshit{
  time: string;
  sold: number;
}
export interface Drinkershit{
  name:string;
}


@Injectable({
  providedIn: 'root'
})
export class BeersService {

  constructor(private http: HttpClient) { }

  getBeers() {
    return this.http.get<any[]>('/api/beer');
  }

  getBarsSelling(beer: string) {
    return this.http.get<BeerLocation[]>(`/api/bars-selling/${beer}`);
  }

  get_top_ten_bars_selling(beer_Name: string)
  {
    return this.http.get<Barshit[]>('/api/beers/'+ beer_Name)
  }
  get_top_ten_drinkers(beer_Name:string)
  {
    return this.http.get<Drinkershit[]>('/api/beers/'  + beer_Name + '/top_drinkers')
  }
  get_hours_beer_sold(beer_Name:string)
  {
    return this.http.get<Hourshit[]>('/api/beers/' + beer_Name + '/hours')
  }
  getBeerManufacturers(beer?: string): any {
    if (beer) {
      return this.http.get<string>(`/api/beer-manufacturer/${beer}`);
    }
    return this.http.get<string[]>('/api/beer-manufacturer');
  }

}
