import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { DrinkerDetailsService, DrinkerDetailsLocation} from '../drinker-details.service';
import { HttpResponse } from '@angular/common/http';

@Component({
  selector: 'app-drinker-details',
  templateUrl: './drinker-details.component.html',
  styleUrls: ['./drinker-details.component.css']
})
export class DrinkerDetailsComponent implements OnInit 
{
  drinker_details: DrinkerDetailsLocation[];
  dName: string;

  constructor(private drinkerService: DrinkerDetailsService, private route: ActivatedRoute) 
  {
      route.paramMap.subscribe((paramMap) => 
      {
        this.dName = paramMap.get('drinker');
        this.getDrinkers();
      });
  }

  getDrinkers()
  {
    this.drinkerService.getDrinkers(this.dName).subscribe(
      data => {
        this.drinker_details = data;
      },
      error => {
        alert('shit not found');
      }
    )
  }


  ngOnInit() 
  {
  }

}