import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';

export interface FactLocation {
  name: string;
  drinker_name: string;
}

@Injectable({
  providedIn: 'root'
})
export class FactsService {

  constructor(private http: HttpClient) { }

  getFacts() {
    return this.http.get<any[]>('/api/fact');
  }


}
