import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';


export interface DrinkerDetailsLocation 
{
  name: string;
  time: string;
  bar_name: string;
}
@Injectable({
  providedIn: 'root'
})
export class DrinkerDetailsService 
{


  constructor(
    public http: HttpClient
  ) { }

  getDrinkers(name: string) {
    return this.http.get<DrinkerDetailsLocation[]>('/api/drinker/' + name);
  }


}
